import type { AxiosResponse } from 'axios'
import axios, { AxiosError } from 'axios'
import type { Dayjs } from 'dayjs'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation, useQuery, useQueryClient } from 'react-query'
import { useNavigate } from 'react-router'

import { COMMON_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/Club'
import type { Conference, ConferenceId } from 'src/models/Conference'
import type { DateProps } from 'src/models/date'

export interface GetParameters {
  clubId: number
  token: string
  date: DateProps
}

export const getSingleConferenceAPICall = async ({ clubId, token, date }: GetParameters, conferenceId: number) => {
  const response = await axios.get<Conference>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference/${conferenceId}?year=${date.year}&semester=${date.semester}`,
    {
      headers: { Authorization: token }
    }
  )
  return response.data
}

export const useGetSingleConference = (clubId: number, date: DateProps, conferenceId: number) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  const conference = useQuery(
    ['singleConference', clubId, date],
    () => getSingleConferenceAPICall({ clubId, token, date }, conferenceId),
    {
      onError: err => {
        if (err instanceof AxiosError) {
          enqueueSnackbar(COMMON_MESSAGE.LOAD_FAIL, { variant: 'error' })
          return
        }
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  )
  return conference
}

export const getConferencesAPICall = async ({ clubId, token, date }: GetParameters) => {
  const response = await axios.get<Conference[]>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference?year=${date.year}&semester=${date.semester}`,
    {
      headers: { Authorization: token }
    }
  )
  return response.data
}

export const useGetConferences = (clubId: number, date: DateProps) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  const conferences = useQuery(['conferences', clubId, date], () => getConferencesAPICall({ clubId, token, date }), {
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(COMMON_MESSAGE.LOAD_FAIL, { variant: 'error' })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    }
  })
  return conferences
}

export interface DeleteParameters {
  clubId: number
  token: string
  conferenceIds: ConferenceId[]
}

export const deleteConferencesAPICall = async ({ clubId, token, conferenceIds }: DeleteParameters) => {
  conferenceIds.forEach(async conferenceId => {
    await axios.delete<null, AxiosResponse<null>, null>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference/${conferenceId}`,
      {
        headers: { Authorization: token }
      }
    )
  })
}

export const useDeleteConference = (clubId: number, conferenceIds: number[], successMessage: string) => {
  const { enqueueSnackbar } = useSnackbar()

  const token = useAuthHeader()()
  const queryClient = useQueryClient()
  const mutationFn = () => deleteConferencesAPICall({ clubId, token, conferenceIds })
  return useMutation(mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(successMessage, { variant: 'success' })
    },
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    },
    onSettled: () => {
      queryClient.invalidateQueries('conferences')
    }
  })
}

export interface PutConferenceProps {
  clubId: ClubId
  conferenceId: number
  title: string
  startAt: Dayjs | null
  users: number[]
  note: string
  clubDate: DateProps
}

export const putConferenceAPICall = async (
  token: string,
  { clubId, conferenceId, title, startAt, users, note, clubDate }: PutConferenceProps
) => {
  await axios.put(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference/${conferenceId}`,
    {
      title: title,
      startAt: startAt,
      users: users,
      note
    },
    {
      params: clubDate,
      headers: { Authorization: token }
    }
  )
}

export const useEditConference = (): UseMutationResult<void, unknown, PutConferenceProps, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (data: PutConferenceProps) => putConferenceAPICall(token, data)
  const navigate = useNavigate()

  return useMutation('putConference', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('conferences')
      navigate(-1)
    },
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    }
  })
}

interface PostConferenceProps {
  clubId: ClubId
  title: string
  startAt: Dayjs | null
  users: number[]
  clubDate: DateProps
}

export const postConferenceAPICall = async (
  token: string,
  { clubId, title, startAt, users, clubDate }: PostConferenceProps
) => {
  await axios.post(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference`,
    {
      title: title,
      startAt: startAt,
      users: users
    },
    {
      params: clubDate,
      headers: { Authorization: token }
    }
  )
}

export const useCreateConference = (data: PostConferenceProps) => {
  const { enqueueSnackbar } = useSnackbar()

  const token = useAuthHeader()()
  const navigate = useNavigate()
  const queryClient = useQueryClient()

  const mutationFn = () => postConferenceAPICall(token, data)

  return useMutation(mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('conferences')
      navigate(`/manage/${data.clubId}/attendance`)
    },
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    }
  })
}

export interface PutAttendanceProps {
  clubId: number
  conferenceId: number
  userId: number
  attendanceType: boolean
  clubDate: DateProps
}

export const putAttendanceAPICall = async (
  token: string,
  { clubId, conferenceId, userId, attendanceType, clubDate }: PutAttendanceProps
) => {
  await axios.put(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference/${conferenceId}/attendance`,
    {
      userId,
      attendanceType
    },
    {
      params: clubDate,
      headers: { Authorization: token }
    }
  )
}

export const useCheckAttendance = (): UseMutationResult<void, unknown, PutAttendanceProps, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const mutationFn = (data: PutAttendanceProps) => putAttendanceAPICall(token, data)

  return useMutation('putAttendance', mutationFn, {
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    }
  })
}
